@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Astloch:400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Astloch:400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Astloch:400&display=swap);
@media (max-width:360px){html{font-size:10px;}}
@media (min-width:361px) and (max-width:399px){html{font-size:11px;}}
@media (min-width:400px) and (max-width:439px){html{font-size:12px;}}
@media (min-width:440px) and (max-width:479px){html{font-size:13px;}}
@media (min-width:480px) and (max-width:519px){html{font-size:14px;}}
@media (min-width:520px) and (max-width:559px){html{font-size:15px;}}
@media (min-width:560px) and (max-width:599px){html{font-size:16px;}}
@media (min-width:600px) and (max-width:639px){html{font-size:17px;}}
@media (min-width:640px) and (max-width:679px){html{font-size:18px;}}
@media (min-width:680px) and (max-width:719px){html{font-size:19px;}}
@media (min-width:720px) and (max-width:759px){html{font-size:20px;}}
@media (min-width:760px) and (max-width:799px){html{font-size:21px;}}
@media (min-width:800px) and (max-width:839px){html{font-size:22px;}}
@media (min-width:840px) and (max-width:879px){html{font-size:23px;}}
@media (min-width:880px){html{font-size:24px;}}

body {
    width: 100vw;
    height: auto;

    margin: 0;
    padding: 0;

    overflow: hidden;

    font-family: 'Quicksand', monospace;
}

.no-drag {
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    user-select: none;
}

::-webkit-scrollbar {
    width: 15px;
}

::-webkit-scrollbar-track {
    background: #272727;
}

::-webkit-scrollbar-thumb {
    background: #474747;
}

::-webkit-scrollbar-thumb:hover {
    background: #555555;
}
.header {
    width: 100%;
    height: 2.5rem;

    position: relative;
    float: left;

    background-color: #101010;
    border-bottom: solid #404040 1px;

    z-index: 10;
}

.header-title {
    width: 5rem;
    height: 2.5rem;
    
    margin-left: 1rem;

    position: relative;
    float: left;

    color: #808080;
    font-size: 1.25rem;
    font-family: 'Astloch', cursive;
    line-height: 2.5rem;

    cursor: pointer;
}

.header-icon {
    width: 1rem;
    height: 1rem;
    margin: 0.75rem 0.75rem 0.75rem 0.75rem;

    position: relative;
    float: left;

    color: #505050;

    cursor: pointer;
}

.header-icon:hover {
    color: #808080;
    -webkit-transition: ease-in 0.25s;
    transition: ease-in 0.25s;
}

.header-icon-active {
    width: 1rem;
    height: 1rem;
    margin: 0.75rem 0.75rem 0.75rem 0.75rem;

    position: relative;
    float: left;

    color: #AAAAAA;

    cursor: pointer;
}
.post {
    width: 30rem;
    height: auto;
    margin-top: 1rem;

    position: relative;
    float: left;

    background-color: #202020;
    border: solid #404040 1px;

    cursor: pointer;
}

.post-profile {
    width: calc(2rem - 2px);
    height: calc(2rem - 2px);
    margin: 0.75rem;

    position: relative;
    float: left;

    background-size: cover;
    background-position: center;

    border: solid #404040 1px;
    border-radius: 50%;
}

.post-info-container {
    width: calc(100% - 4.25rem);
    height: 2rem;
    margin: 0.75rem 0.75rem 0.75rem 0;

    position: relative;
    float: left;
}

.post-author {
    width: 50%;
    height: 1rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.9rem;
    font-weight: 700;

    opacity: 0.75;
}

.post-time {
    width: 50%;
    height: 1rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    font-weight: 400;
    text-align: right;

    opacity: 0.5;
}

.post-in , .post-with{
    width: auto;
    height: 1rem;

    margin-right: 0.35rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.5rem;
    font-weight: 400;
    line-height: 1.3rem;

    opacity: 0.25;
}

.post-location, .post-people {
    width: auto;
    height: 1rem;

    margin-right: 0.35rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    font-weight: 400;
    line-height: 1.25rem;

    opacity: 0.5;
}

.post-picture {
    width: 30rem;
    height: 30rem;

    position: relative;
    float: left;

    background-size: cover;
    background-position: center;

    border-top: solid #404040 1px;
    border-bottom: solid #404040 1px;
}

.post-reflect-container {
    width: 100%;
    height: auto;

    position: relative;
    float: left;
}

.post-icon {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem 0rem 0.5rem 0.5rem;

    position: relative;
    float: left;

    color: #505050;

    cursor: pointer;
}

.post-icon-active {
    color: #FF4081;
}

.post-icon:nth-child(2) {
    margin-left: calc(100% - 6rem);
}

.post-icon:hover {
    color: #808080;
    -webkit-transition: ease-in 0.25s;
    transition: ease-in 0.25s;
}

.post-content {
    width: calc(100% - 1rem);
    height: auto;
    resize: none;
    overflow: hidden;

    padding: 0.1rem 0.5rem 0.25rem 0.5rem;
    margin-bottom: 0.25rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    font-weight: 700;
    line-height: 1rem;
    background: none;
    border: none;
    outline: none;

    opacity: 0.5;
}

.post-likes, .post-comments {
    width: auto;
    height: 0.75rem;

    padding: 0 0 0 0.5rem;
    margin-bottom: 0.75rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    line-height: 1rem;

    cursor: pointer;
    outline: none;

    opacity: 0.25;
}

.post-comment-box {
    width: calc(100% - 4rem);
    height: 1rem;
    resize: none;
    overflow: hidden;

    padding: 0.5rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    font-weight: 700;
    line-height: 1rem;
    background: none;
    border: none;
    border-top: solid 1px #404040;
    outline: none;

    opacity: 0.5;
}

.post-comment-post {
    width: 3rem;
    height: 2rem;

    position: relative;
    float: left;

    color: #CCCCCC;
    font-size: 0.6rem;
    font-weight: 900;
    line-height: 2rem;
    text-align: center;
    border-top: solid 1px #404040;

    cursor: pointer;
    outline: none;

    opacity: 0.5;
}

@media (max-width: 50rem) {
    .post {
        width: 100%;
    }

    .post-picture {
        width: calc(95vw - 15px);
        height: calc(95vw - 15px);

        max-width: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }
}
.search {
    width: 100%;
    height: calc(100% - 5.5rem + 2px);
    padding-bottom: 0.5rem;

    overflow-x: hidden;
    overflow-y: scroll;

    position: fixed;
    top: calc(2.5rem + 1px);
    left: 0;

    background-color: #202020;

    z-index: 2;
}

.search-button-container {
    width: 6rem;
    height: 2rem;
    margin: 1rem 0;

    position: relative;
    float: left;

    background-color: #303030;
    border-top: solid 0.05rem #404040;
    border-left: solid 0.05rem #404040;
    border-bottom: solid 0.05rem #404040;
}

.search-button {
    width: 2.49rem;
    height: 0.99rem;
    margin-top: 0.5rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.5rem;
    font-weight: 500;
    text-align: center;
    line-height: 0.99rem;
    border: solid 0.05rem #606060;

    opacity: 0.5;

    cursor: pointer;
}

.search-button:nth-child(1) {
    margin-left: 0.5rem;
    border-right: none;
    border-radius: 0.5rem 0 0 0.5rem;
}

.search-button:nth-child(2) {
    border-radius: 0 0.5rem 0.5rem 0;
}

.search-button-active {
    color: black;
    background-color: white;
}

.search-input {
    width: 22.9rem;
    height: 2rem;
    margin: 1rem 0;
    padding: 0 0.5rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.7rem;
    font-weight: 700;
    background-color: #303030;
    border: none;
    border-top: solid 0.05rem #404040;
    border-right: solid 0.05rem #404040;
    border-bottom: solid 0.05rem #404040;

    cursor: pointer;
    outline: none;
}


.search-info {
    width: 28rem;
    height: 1rem;
    margin-top: 0;

    position: relative;
    float: left;

    color: white;
    font-size: 0.7rem;
    font-weight: 500;
    text-align: center;
    background: none;
    border: none;

    opacity: 0.25;

    cursor: pointer;
    outline: none;
}

.search-user-container {
    width: 100%;
    height: 3rem;
    margin: 0 0 0.5rem 0;

    position: relative;
    float: left;

    border: solid 0.05rem #404040;

    cursor: pointer;
}

.search-user-profile {
    width: 1.9rem;
    height: 1.9rem;
    margin: 0.5rem;

    position: relative;
    float: left;

    background-size: cover;
    background-position: center;

    border: solid 0.05rem #404040;
    border-radius: 50%;
}

.search-user-nickname {
    width: auto;
    max-width: 10rem;
    height: 3rem;
    margin: 0 0 0 0.5rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.9rem;
    font-weight: 900;
    line-height: 2.9rem;

    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    opacity: 0.75;
}

.search-user-bio {
    width: auto;
    max-width: 10rem;
    height: 3rem;

    margin: 0 0 0 1rem;

    position: relative;
    float: left;

    color: white;
    font-size: 0.6rem;
    font-weight: 700;
    line-height: 3.1rem;

    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    opacity: 0.5;
}

.search-post-container {
    width: 100%;
    height: auto;

    position: relative;
    float: left;
}

.search-post {
    width: calc(9.66rem - 2px);
    height: calc(9.66rem - 2px);
    margin: 0 0.5rem 0.5rem 0;

    position: relative;
    float: left;

    object-fit: cover;

    background-size: cover;
    background-position: center;

    border: solid 1px #404040;

    cursor: pointer;
}

.search-post:nth-child(3n) {
    margin-right: 0;
}

@media (max-width: 50rem) {
    .search-input {
        width: calc(100% - 7rem - 2px);
    }

    .search-info {
        width: 100%;
    }

    .search-post {
        width: calc((95vw - 15px) / 3 - 0.5rem * 2 / 3 - 2px);
        height: calc((95vw - 15px) / 3 - 0.5rem * 2 / 3 - 2px);
    }
}
.camera-loading {
    width: 30rem;
    height: 30rem;
    margin-top: 3.5rem;

    position: absolute;
    top: 0;
    left: 0;

    background-color: #272727;
    border-top: solid #404040 1px;
    border-bottom: solid #404040 1px;
}

.camera-loading-img {
    width: 1.5rem;
    height: 1.5rem;

    position: absolute;
    top: calc(50% - 0.75rem);
    left: calc(50% - 0.75rem);

    border: 0.35rem solid #707070;
    border-radius: 50%;
    border-top: 0.35rem solid #AAAAAA;
    
    animation: spin 1.5s linear infinite;
    -webkit-animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (max-width: 50rem) {
    .camera-loading {
        width: calc(95vw - 15px);
        height: calc(95vw - 15px);

        max-width: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }
}
.camera-gallery {
    width: 30rem;
    height: calc(5rem + 15px);

    position: relative;
    float: left;

    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;

    background-color: #303030;
    border-bottom: solid #404040 1px;
}

.camera-gallery > p {
    width: 30rem;
    height: 5rem;
    padding: 0;
    margin: 0;

    color: #505050;
    font-size: 0.6rem;
    font-weight: 900;
    text-align: center;
    line-height: 5rem;
}

.camera-gallery-image {
    width: 5rem;
    height: 5rem;

    display: inline-block;

    object-fit: cover;
    background-size: cover;
    background-position: center;

    opacity: 0.35;

    cursor: pointer;
}

.camera-gallery-image-active {
    width: 5rem;
    height: 5rem;

    display: inline-block;

    object-fit: cover;
    background-size: cover;
    background-position: center;

    cursor: pointer;
}

@media (max-width: 50rem) {
    .camera-gallery {
        width: 100%;
    }
}
.preview {
    width: 30rem;
    height: 30rem;

    position: relative;
    float: left;

    border-top: solid #404040 1px;
    border-bottom: solid #404040 1px;
}

.preview-image {
    width: 100%;
    height: 100%;

    object-fit: cover;
    background-size: cover;
    background-position: center;

    cursor: pointer;

    z-index: 3;
}

.preview-rollback {
    width: 2rem;
    height: 2rem;

    position: absolute;
    top: 26rem;
    left: calc(50% - 1.5rem);

    color: #808080;
    background-color: #404040;
    border-radius: 50%;
    border: solid 0.5rem #404040;

    opacity: 0.75;

    cursor: pointer;

    z-index: 5;
}

.preview-filter {
    width: 2rem;
    height: 2rem;

    position: absolute;
    top: 26.5rem;
    left: 1.5rem;

    object-fit: cover;
    background-size: cover;
    background-position: center;

    border: solid #404040 1px;
    border-radius: 0.5rem;

    z-index: 6;
}

.preview-filter-1 {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}

.preview-filter-2 {
    -webkit-filter: sepia(100%);
    filter: sepia(100%);
}

.preview-filter-title {
    width: auto;
    height: calc(2rem + 2px);
    padding: 0 0.75rem 0 1.75rem;

    position: absolute;
    top: 26.5rem;
    left: 2.5rem;

    color: #AAAAAA;
    font-size: 0.6rem;
    font-weight: 500;
    line-height: 2rem;
    background-color: #202020;
    border-radius: 0.5rem;

    opacity: 0.85;

    z-index: 5;
}

.preview-upload {
    width: 1rem;
    height: 1rem;
    padding: 0.5rem;

    position: absolute;
    top: 1.5rem;
    left: calc(100% - 3.5rem);

    color: #AAAAAA;
    background-color: #202020;
    border-radius: 0.5rem;

    opacity: 0.85;
}

.preview-delete {
    width: 1rem;
    height: 1rem;
    padding: 0.5rem;

    position: absolute;
    top: 1.5rem;
    left: calc(100% - 3.5rem);

    object-fit: cover;

    color: #808080;
    background-color: #202020;
    border-radius: 0.5rem;

    opacity: 0.85;

    z-index: 5;
}

@media (max-width: 50rem) {
    .preview {
        width: calc(95vw - 15px);
        max-width: calc(95vw - 15px);

        height: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }

    .preview-image {
        width: calc(95vw - 15px);
        max-width: calc(95vw - 15px);

        height: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }

    .preview-rollback {
        top: calc(95vw - 15px - 4.5rem);
    }

    .preview-filter {
        top: calc(95vw - 15px - 3.75rem);
    }

    .preview-filter-title {
        top: calc(95vw - 15px - 3.75rem);
    }
}
.sticker {
    width: calc(100% - 1rem);
    height: calc(1.5rem + 15px);
    padding: 0.5rem;
    overflow-x: scroll;
    overflow-y: hidden;

    position: relative;
    float: left;

    white-space: nowrap;

    border-bottom: solid #404040 1px;
}

.sticker-image {
    width: 5%;
    height: 1.5rem;

    display: inline-block;

    background-size: cover;
    background-position: center;
}
.canvas {
    width: 30rem;
    height: 30rem;
    overflow: hidden;

    position: absolute;
    top: 3.5rem;
    left: 0;

    z-index: 4;
}

.canvas-sticker {
    width: 3rem;
    height: 3rem;

    position: absolute;
    top: 0;
    left: 0;

    background-size: cover;
    background-position: center;
}

@media (max-width: 50rem) {
    .canvas {
        width: calc(95vw - 15px);
        height: calc(95vw - 15px);

        max-width: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }
}
.camera {
    width: 100%;
    height: calc(100% - 5rem + 2px);

    overflow-x: hidden;
    overflow-y: scroll;

    position: fixed;
    top: calc(2.5rem + 1px);
    left: 0;

    background-color: #202020;
    opacity: 0.975;

    z-index: 2;
}

.camera-post {
    width: 30rem;
    height: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;

    position: relative;
    float: left;

    background-color: #202020;
    border: solid #404040 1px;

    cursor: pointer;
}

.camera-input {
    width: 7rem;
    height: 0.7rem;

    color: white;
    font-size: 0.6rem;
    background: none;
    border: none;
    border-bottom: solid 0.1rem #404040;

    outline: none;
}

.camera-webcam {
    width: 30rem;
    height: 30rem;

    position: relative;
    float: left;

    object-fit: cover;
    background-size: cover;
    background-position: center;

    border-top: solid #404040 1px;
    border-bottom: solid #404040 1px;
}

.camera-shoot {
    width: 2rem;
    height: 2rem;

    position: absolute;
    top: 29.5rem;
    left: calc(50% - 1.5rem);

    background-color: #AAAAAA;
    border-radius: 50%;
    border: solid 0.5rem #404040;

    opacity: 0.75;

    cursor: pointer;

    z-index: 9;
}

.camera-shoot:hover {
    opacity: 1;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.camera-shutter {
    width: 30rem;
    height: 30rem;
    overflow: hidden;

    position: absolute;
    top: 3.5rem;
    left: 0;

    background-color: #606060;

    opacity: 0;
    -webkit-transition: all 150ms ease-in;
    transition: all 150ms ease-in;

    z-index: 1;
}

.camera-shutter-on {
    opacity: 1;
    -webkit-transition: all 150ms ease-in;
    transition: all 150ms ease-in;
}

.camera-margin {
    width: 100%;
    height: 2rem;

    position: relative;
    float: left;

    background-color: #272727;
}

.camera-icon {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem 0rem 0.5rem 0.5rem;

    position: relative;
    float: left;

    color: #505050;

    cursor: pointer;
}

.camera-icon:nth-child(2) {
    margin-left: calc(100% - 3rem);
}

.camera-icon:hover {
    color: #808080;
    -webkit-transition: ease-in 0.25s;
    transition: ease-in 0.25s;
}

.camera-icon-wide {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem calc(100% - 1.5rem) 0.5rem 0.5rem;

    position: relative;
    float: left;

    color: #505050;

    cursor: pointer;
}

@media (max-width: 50rem) {
    .camera-post {
        width: 100%;
        max-width: 100%;
    }

    .camera-webcam {
        width: calc(95vw - 15px);
        max-width: calc(95vw - 15px);

        height: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }

    .camera-shoot {
        top: calc(95vw - 15px - 1rem);
    }

    .camera-shutter {
        width: calc(95vw - 15px);
        max-width: calc(95vw - 15px);

        height: calc(95vw - 15px);
        max-height: calc(95vw - 15px);
    }
}
.message {
    width: 100%;
    height: 3rem;
    margin-top: 1rem;

    position: relative;
    float: left;

    background-color: #303030;
    border: solid #404040 1px;
}

.message-profile {
    width: 2rem;
    height: 2rem;
    margin: 0.5rem 0.75rem 0.5rem 0.5rem;

    position: relative;
    float: left;

    background-size: cover;
    background-position: center;
    
    border: solid 1px #404040;
    border-radius: 50%;

    cursor: pointer;
}

.message-author {
    width: auto;
    height: 3rem;
    margin: 0 0.4rem 0 0;

    position: relative;
    float: left;

    color: white;
    font-size: 0.7rem;
    font-weight: 600;
    line-height: 3rem;

    opacity: 0.5;

    cursor: pointer;
}

.message-content {
    width: auto;
    height: 3rem;

    position: relative;
    float: left;

    color: white;
